/* Dashboard 스타일 */
.dashboard {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: var(--card-shadow);
}

/* 탭 네비게이션 */
.tab-navigation {
  display: flex;
  background: var(--light-color);
  border-bottom: 1px solid var(--border-color);
  overflow-x: auto;
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 1.5rem;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
  transition: all 0.2s;
  white-space: nowrap;
}

.tab-button:hover {
  background: rgba(37, 99, 235, 0.1);
  color: var(--primary-color);
}

.tab-button.active {
  background: var(--primary-color);
  color: white;
  border-bottom: 3px solid var(--secondary-color);
}

.tab-icon {
  font-size: 1.1rem;
}

/* 대시보드 내용 */
.dashboard-content {
  padding: 2rem;
}

/* 로딩 및 오류 상태 */
.dashboard-loading,
.dashboard-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--border-color);
  border-left: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* 개요 탭 스타일 */
.overview-tab {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.stat-card {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: var(--card-shadow);
  transition: transform 0.2s, box-shadow 0.2s;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 15px -3px rgba(0, 0, 0, 0.1);
}

.stat-icon {
  font-size: 2.5rem;
  background: rgba(37, 99, 235, 0.1);
  padding: 0.75rem;
  border-radius: 0.5rem;
}

.stat-content h3 {
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.25rem;
}

.stat-number {
  color: var(--text-primary);
  font-size: 1.875rem;
  font-weight: 700;
  margin: 0;
}

.charts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
}

.chart-card {
  background: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: var(--card-shadow);
}

.chart-card h3 {
  color: var(--text-primary);
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.chart-container {
  position: relative;
  height: 300px;
}

/* 분석 탭 스타일 */
.analytics-tab h2 {
  color: var(--text-primary);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.analytics-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.analysis-card {
  background: white;
  padding: 2rem;
  border-radius: 0.75rem;
  box-shadow: var(--card-shadow);
  transition: transform 0.2s;
}

.analysis-card:hover {
  transform: translateY(-2px);
}

.analysis-card h3 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.125rem;
  font-weight: 600;
}

.analysis-card p {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* 성능 탭 스타일 */
.performance-tab h2 {
  color: var(--text-primary);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.performance-metrics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.metric-card {
  background: white;
  padding: 1.5rem;
  border-radius: 0.75rem;
  box-shadow: var(--card-shadow);
}

.metric-card h4 {
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.metric-value {
  color: var(--text-primary);
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.metric-bar {
  width: 100%;
  height: 8px;
  background: var(--border-color);
  border-radius: 4px;
  overflow: hidden;
}

.metric-fill {
  height: 100%;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.metric-fill.cpu {
  background: linear-gradient(90deg, #10b981, #059669);
}

.metric-fill.memory {
  background: linear-gradient(90deg, #3b82f6, #2563eb);
}

.metric-fill.disk {
  background: linear-gradient(90deg, #f59e0b, #d97706);
}

/* 모니터링 탭 스타일 */
.monitoring-tab h2 {
  color: var(--text-primary);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.monitoring-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.monitor-card {
  background: white;
  padding: 2rem;
  border-radius: 0.75rem;
  box-shadow: var(--card-shadow);
}

.monitor-card h3 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.125rem;
  font-weight: 600;
}

.status-indicator {
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-weight: 500;
  font-size: 0.875rem;
}

.status-indicator.connected {
  background: rgba(16, 185, 129, 0.1);
  color: var(--success-color);
  border: 1px solid var(--success-color);
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .dashboard-content {
    padding: 1rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .charts-grid {
    grid-template-columns: 1fr;
  }
  
  .chart-card {
    padding: 1rem;
  }
  
  .chart-container {
    height: 250px;
  }
  
  .tab-button {
    padding: 0.75rem 1rem;
    font-size: 0.75rem;
  }
  
  .tab-label {
    display: none;
  }
  
  .tab-icon {
    font-size: 1.25rem;
  }
}

@media (max-width: 480px) {
  .performance-metrics,
  .analytics-content,
  .monitoring-content {
    grid-template-columns: 1fr;
  }
  
  .metric-value {
    font-size: 1.5rem;
  }
}